.header {
    display: flex;
    justify-content: space-between;
    background-color: rgb(15, 15, 15);
    height: 100px;
    max-width: 1920px;
}
.language{
    display: flex;
    gap: 12px;
    margin-bottom: 4px;
    border-radius: 64px;
    display: flex;
    justify-content: center;
    margin-right: 32px;


    cursor: pointer;
    button{
        font-family: 'Baloo 2';
        font-weight: bold;
        border-radius: 64px;
        background-color: #4AD592;
        &:hover{
        background-color: #59eca5;
        color: whitesmoke;
        text-shadow: 1px 1px 3px black;
    }}
}

.burgermenu_background {
    background-color: rgba(1, 1, 1, 0.675);
    height: 100vh;
    width: 100vw;
    z-index: 999;
    position: absolute;
    right: 0;
    top: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    display: none;
}

.burgermenu_background.show-menu {
    display: flex;
}
.burgermenu_background.close-menu {
    display: none;
}

.burgermenu {
    height: 100vh;
    width: 100vw;
    background-color: #0f0f0f36;
    font-family: 'Baloo 2';

}

.burgermenu_img img {
    width: 80px;
    border-radius: 80px;
    margin-left: 36px;
}

#burger_img {
    height: 32px;
    width: 32px;
    display: none;
    position: absolute;
    top: 8px;
    right: 28px;
    cursor: pointer;
}


.navbar {
    width: 418px;
    height: 32px;
    margin-top: 42.5px;
    width: auto;
    margin-right: 100px;
display: flex;
    a {
        margin-left: 28px;
        box-shadow: inset 0 0 0 0 #4AD592;
        color: #4AD592;

        transition: color .3s ease-in-out, box-shadow .3s ease-in-out;
        text-decoration: none;
        cursor: pointer;
        text-shadow: 1px 1px 4px rgb(4, 4, 4);

        font-size: 18px;
        font-weight: bold;
        font-family: 'Montserrat';
padding: 3px;

        &:hover {
            font-weight: bold;
            color: #fff;
            box-shadow: inset 200px 0 0 0 #4AD592;
            border-radius: 6px;

        }
    }
}

.burger_links {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 24px;
    margin-top: 100px;

      
  
}


.burgermenu_img {
    display: flex;
    justify-content: space-between;
    margin-top: 8px;

    button {
        margin-top: 12px;
        margin-right: 24px;
        width: 56px;
        font-size: 24px;
        font-weight: bold;
        background-color: #4ad592d4;
        border-radius: 164px;
margin-bottom: 10px;
&:hover{
    color: whitesmoke;
    text-shadow: 1px 1px 3px black;
}
    }
}


.burger_links a {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    color: #4AD592;
    border: 2px solid #4AD592;
    border-radius: 7px;
    height: 58px;
    width: 160px;
    background-color: #0F0F0F;
    font-size: 23px;
    font-weight: bold;
    transition: background-color 1.1s;

    &:hover {
        background-color: #4AD592;
        color: whitesmoke;
        background-position: top left;
text-shadow: 1px 1px 4px black;
    }

    background: linear-gradient(to top left, transparent 50%, #4AD592 50%);
    background-size: 200% 200%;
    background-position: bottom right;
    color: #4AD592;
    transition: all 0.25s ease;
}



.hero_circle_zindex {
    height: 680px;
    width: 567px;
    margin-left: 0;
    position: relative;
    top: 0;
}

.logo_zindex {
    z-index: 44;
    position: absolute;
    left: 80px;
    width: 80px;
    border-radius: 524px;
    margin-top: 8px;



}

.span {
    line-height: 0.8;
    margin-top: 0;
    margin-bottom: 0;
    gap: 0;
    font-size: 60px;
    font-weight: bold;
    font-family: 'Baloo 2';
    text-shadow: 1px 1px 4px rgb(4, 4, 4);
    color: black;
    z-index: 99;
    margin-left: 90px;
}

.span1 {
    margin-left: 93px;
    margin-top: 99px;
    margin-bottom: 0;
    font-size: 31px;
    text-shadow: 1px 1px 4px rgb(4, 4, 4);
    font-family: 'Montserrat';
    color: black;
    z-index: 99;
}

.spanposition {
    display: flex;
    flex-direction: column;

    position: absolute;
    display: none;
    margin-top: 20px;
}
